$(".filter-group__more").click(function () {
    $(this).parents(".filter-group").addClass("_all-show")
    $(this).hide()
})

$(".filter-group__show").click(function () {
    $(this).parents(".filter-group").addClass("_active")
    $(this).siblings(".filter-group__hidden").slideToggle()
})

$(".mob-filter__btn").click(function () {
    $(".filter").addClass("_active")
    $("body").addClass("fixed-body")
})

$(".filter__bg").click(function () {
    $(".filter").removeClass("_active")
    $("body").removeClass("fixed-body")
})

$(".guide-card__show").click(function () {
    $(this).parents(".guide-card").addClass("_active")
    $(this).siblings(".guide-card__hidden").slideToggle()
})
