function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        autoplay: {
            disableOnInteraction: false,
            pauseOnMouseEnter: true,
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function collectionSlider() {
    var swiper = new Swiper('.collection-slider__container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        watchSlidesProgress: true,
        preventClicks :true,
        a11y: false,
        noSwipingSelector: "button",
        navigation: {
            nextEl: '.collection-slider .swiper-button-next',
            prevEl: '.collection-slider .swiper-button-prev',
        },
    })
}



function Sliders() {
    let SlidersArray = []
    $("._slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`._slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            noSwipingSelector: "button",
            navigation: {
                nextEl: `._slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `._slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `._slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

function productCardSliders() {
    let SlidersArray = []
    $(".product-card").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.product-card[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            effect: 'fade',
            // allowTouchMove: false,
            // nested: true,
            pagination: {
                el: `.product-card[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
            breakpoints: {

            }
        })

        $(`.product-card[data-slider-id="${index}"] .swiper-pagination-bullet`).mouseover(function () {
            $(this).click()
        })
    })
}

function roomsSlider() {
    var swiper = new Swiper(".room-items .swiper-container", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
    });
    var swiper2 = new Swiper(".rooms-media .swiper-container", {
        spaceBetween: 0,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.room-nav .swiper-button-next',
            prevEl: '.room-nav .swiper-button-prev',
        },
    });

    $(`.room-item`).each(function (i,el) {
        $(el).mouseover(function () {
            swiper2.slideTo(i)
        })
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
        direction: "vertical",
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        thumbs: {
            swiper: swiper,
        },
        pagination: {
            el: '.gallery .swiper-pagination',
            type: 'bullets',
            clickable: true,
            dynamicBullets: true,
            dynamicMainBullets: 5,
        },
    });
}


mainSlider()
Sliders()
collectionSlider()
productCardSliders()
roomsSlider()
gallery()
