let fancybox = document.querySelectorAll("[data-fancybox]")

Fancybox.defaults.placeFocusBack = false

$(".favourite-btn").click(function () {
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
    } else{
        $(this).addClass("_active")
        renderCartAlert($(this).parents(".product-card"))
    }
})


function renderCartAlert(product) {
    let imgSrc = $(product).find(".swiper-slide:nth-child(1) img").attr("src")
    let title = $(product).find(".product-card__title").text()
    $(".cart-alert__img img").attr("src",imgSrc)
    $(".cart-alert__title").html(title)
    $(".cart-alert").addClass("_active")

    setTimeout(()=>{
        $(".cart-alert").removeClass("_active")
    },10000)
}

$(".cart-alert__close").click(function () {
    $(".cart-alert").removeClass("_active")
})

$(".fg input").each(function(index, el) {
    InputIsFilled($(el))
});

$(".fg textarea").each(function(index, el) {
    InputIsFilled($(el))
});

$(".fg select").each(function(index, el) {
    InputIsFilled($(el))
});

$(".fg input").change(function() {
    InputIsFilled($(this))
})

$(".fg textarea").change(function() {
    InputIsFilled($(this))
})

$(".fg select").change(function() {
    InputIsFilled($(this))
})

function InputIsFilled(el) {
    if ($(el).val()) {
        $(el).addClass("input-active")
    } else {
        $(el).removeClass("input-active")
    }
}

$(".pass-toggle").click(function () {
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        $(this).siblings("input").attr("type","password")
    } else{
        $(this).addClass("_active")
        $(this).siblings("input").attr("type","text")
    }
})
