$(document).ready(function() {

    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        $(".fixed-body2").css("padding-right",`${getScrollBarSize()[0]}px`)
    })

});



function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}
