$(".nav__show").click(function () {
    $("._nav-parent").removeClass("_active")
    $(this).parents("._nav-parent").toggleClass("_active")
})

$(document).mouseup(function(e) {
    var div = $('._nav-parent');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("_active")){
            $(div).removeClass("_active")
        }
    }
});

$(".main").click(function () {
    $(".nav__item").removeClass("_active")
})

$(".nav .nav__show").click(function () {
    if(window.innerWidth < 993){
        $(this).siblings(".nav-dropdown").slideToggle()
    }
})

$(".user__btn").click(function() {
    $(".profile-sidebar").addClass("_active")
    $("body").addClass("fixed-body")
})

$(".profile-sidebar__close").click(function() {
    $(".profile-sidebar").removeClass("_active")
    $("body").removeClass("fixed-body")
})

$(".burger").click(function() {
    $(".header").addClass("_open")
    $("body").addClass("fixed-body")
})

$(".menu__close").click(function() {
    $(".header").removeClass("_open")
    $("body").removeClass("fixed-body")
})

$("._search-btn").click(function() {
    body.style.paddingRight = `${getScrollBarSize()[0]}px`
    $(".header").addClass("_open-search")
    $("body").addClass("fixed-body")
})

$("._close-search").click(function() {
    body.style.paddingRight = `0`
    $(".header").removeClass("_open-search")
    $("body").removeClass("fixed-body")
})
